import bdService from '@/components/page-component/goods/bd-service.vue';

    export default {
        name: 'app-bottom-button',
	    
	    props: {
            selectAttr: Object,
            selectGroupAttrId: {
                type: [String, Number]
            },
            is_alone_buy: {
                type: Number,
            },
			groups: {
                type: Array,
	            default() {
                    return [];
	            }
            },
            level_show: {
                type: Number,
	            default() {
                    return 0;
                }
            },
			member_price_min: {
                type: [String, Number],
                default() {
                    return '0.00';
                }
            },

            price: {
                type: String,
	            default() {
                    return '0.00';
	            }
            },
            price_member_min: {
                type: Number,
	            default() {
                    return 0;
                }
            },
		    pt: {
                type: Boolean,
		    },
            item: Object,
			start_time: Boolean,
			end_time: Boolean,
			theme: Object,
            url: String,
            name: String,
            singlePrice: String
	    },
	    
	    data() {
            return {
                group_price: '',
				disable: 'disable'
            }
	    },
	    mounted() {
			this.get_group();
	    },
	    methods: {
            turnOn(bool) {
                this.$emit('click', bool);
            },
		    get_group() {
                if (this.$validation.isEmpty(this.selectAttr)) {
                    if (this.level_show === 1) {
                        if (Number(this.member_price_min) === 0) {
                            this.group_price = '免费';
                        } else {
                            this.group_price = `￥${this.member_price_min}`;
                        }
                    } else {
                        if (Number(this.groups[0].price_min) ===  0) {
                            this.group_price =  '免费';
                        } else {
                            this.group_price = `￥${this.groups[0].price_min}`;
                        }
                    }
                } else {
                    if (this.level_show === 1) {
                        if (Number(this.selectAttr.price_member) === 0) {
                            this.group_price = '免费';
                        } else {
                            this.group_price = `￥${this.selectAttr.price_member}`;
                        }
                    } else {
                        if (Number(this.selectAttr.price) ===  0) {
                            this.group_price = '免费';
                        } else {
                            this.group_price = `￥${this.selectAttr.price}`;
                        }
                    }
                }
		    },
            router() {
                uni.reLaunch({
                    url: `/pages/index/index`
                });
            }
	    },
	    computed: {
            // // 单价
            // singlePrice() {
            //     if (this.pt) return;
            //     if (this.$validation.isEmpty(this.selectAttr)) {
            //         if (this.level_show === 1) {
            //             if (this.price_member_min === 0) {
            //                 return '免费';
            //             } else {
            //                 let price = this.price_member_min;
            //                 return `￥${price.toFixed(2)}`
            //             }
            //         } else {
            //             if (Number(this.price) ===  0) {
            //                 return '免费';
            //             } else {
            //                 return `￥${this.price}`
            //             }
            //         }
            //     } else {
            //         if (this.level_show === 1) {
            //             if (Number(this.selectAttr.price_member) === 0) {
            //                 return '免费';
            //             } else {
            //                 return `￥${this.selectAttr.price_member}`;
            //             }
            //         } else {
            //             if (Number(this.price) ===  0) {
            //                 return '免费';
            //             } else {
            //                 return `￥${this.selectAttr.price}`
            //             }
            //         }
            //     }
            // },
		    
		    set_group_num() {
                for (let i = 0; i < this.groups.length; i++) {
                    if (this.groups[i].groups.id == this.selectGroupAttrId) {
                        return i;
                    }
                }
		    }	    },
	    
	    watch: {
            selectAttr: {
                handler() {
                    if (this.pt) {
                        if (this.$validation.isEmpty(this.selectAttr)) {
                            if (this.level_show === 1) {
                                if (Number(this.member_price_min) === 0) {
                                    this.group_price = '免费';
                                } else {
                                    this.group_price =  `￥${this.member_price_min}`;
                                }
                                
                            } else {
                                if (Number(this.groups[0].price_min) ===  0) {
                                    this.group_price =  '免费';
                                } else {
                                    this.group_price =  `￥${this.groups[0].price_min}`;
                                }
                            }
                            
                        } else {
                            if (this.level_show === 1) {
                                if (Number(this.selectAttr.price_member) === 0) {
                                    this.group_price =  '免费';
                                } else {
                                    this.group_price =  `￥${this.selectAttr.price_member}`;
                                }
                            } else {
                                if (Number(this.selectAttr.price) ===  0) {
                                    this.group_price =  '免费';
                                } else {
                                    this.group_price =  `￥${this.selectAttr.price}`;
                                }
                            }
                        }
                    }
                },
	            immediate: true,
	            deep: true,
            },

			groups: {
            	handler(data) {
            		if (data.length > 0) {
						this.$nextTick(() => {
							this.get_group();
						})
					}
				},
				immediate: true
			}
	    },
        components: {
            bdService
